(function() {
  'use strict';

  angular
    .module('hqReader')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController(moment) {
      var vm = this;

      // "vm.creationDate" is available by directive option "bindToController: true"
      vm.relativeDate = moment(vm.creationDate).fromNow();
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('hqReader')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('hqReader')
    .component('hqViewerEditor', {
      controller: hqViewerEditorCtrl,
      templateUrl: "app/components/hqViewerEditor/hqViewerEditor.html",
      bindings:{
        imgPlaceholder: "@"
      }
    });

  /** @ngInject */
  function hqViewerEditorCtrl($scope, $element, hotkeys){
    var self = this;

    self.scenes = [];

    self.newScene = {
      translateX: 0,
      translateY: 0,
      translateZ: 0
    }

    self.position = undefined;

    self.onSelect = function(file){
      self.image = file
    }

    self.toScene = function(scene){
      if(angular.isDefined(scene)){
        $element.find('.inner').attr(
          'style', 'transform: translate3d('+ scene.translateX +'%, '+ scene.translateY +'%, '+ scene.translateZ +'px);'
        )
      } else {
        self.toOverview()
      }

      self.scene = scene

      return false;
    }

    self.nextScene = function(){
      if(angular.isDefined(self.position)){
        if( self.position < (self.scenes.length - 1)){
          self.position++;
        } else{
          self.toOverview();
        }
      } else{
        self.position = 0;
      }
      // Navigate to Scene
      self.toScene(self.scenes[self.position]);
    };

    self.backScene = function(){
      if(angular.isDefined(self.position)){
        if( self.position > 0){
          self.position--
        } else{
          return self.toOverview()
        }
      }
      // Navigate to Scene
      self.toScene( self.scenes[self.position] );
    };

    self.toOverview = function(){
      // reset variables
      self.position = undefined;
      self.scene = undefined;
      // Return to overview
      $element.find('.inner').attr(
        'style', 'transform: translate3d(0px,0px,0px);'
      );
    };

    self.updateCamera = function(){
      $element.find('.inner').attr(
        'style', 'transform: translate3d('+ self.newScene.translateX +'%, '+ self.newScene.translateY +'%, '+ self.newScene.translateZ +'px);'
      );
      return false
    }

    self.reset = function(){
      self.newScene = {
        translateX: 0,
        translateY: 0,
        translateZ: 0
      }

      $element.find('.inner').attr(
        'style', 'transform: translate3d(0px,0px,0px);'
      );
    }

    self.save = function(){
      self.scenes.push(angular.copy(self.newScene))
      self.reset()
    }

    // Events
    $element.find('.inner img').on("mousemove", function(event){
      var parentOffset = angular.element(this).parent().offset()
      var elementOffset = angular.element(this).offset()

      var relX = event.pageX - parentOffset.left;
      var relY = event.pageY - parentOffset.top;

      var centerX = elementOffset.left + angular.element(this).width() / 2;
      var centerY = elementOffset.top + angular.element(this).height() / 2;

      console.log(centerX, centerY);

      self.pageX = parseInt(relX)
      self.pageY = parseInt(relY)

      $scope.$apply()
    });

    // Hotkeys

    hotkeys.bindTo($scope)
      .add({
        combo: 'right',
        description: 'To next scene',
        callback: function() {
          self.nextScene()
        }
      })
      .add({
        combo: 'left',
        description: 'To next scene',
        callback: function() {
          self.backScene()
        }
      });

    return self;
  }

})();

(function() {
  'use strict';

  angular
    .module('hqReader')
    .component('hqViewer', {
      controller: hqViewerCtrl,
      templateUrl: "app/components/hqViewer/hqViewer.html",
      bindings: {
        placeholder: "@",
        manifest: "@"
      }
    });

  /** @ngInject */
  function hqViewerCtrl($scope, $element, $http, $log, hotkeys){
    var self = {
      slideIndex: 0
    };

    self.magazineImages = [];

    self.position = undefined

    self.$onInit = function(){
      $http.get(self.manifest).then(function(response){
        self.magazineImages = response.data;
      })
    }

    self.toScene = function(scene){
      if(angular.isDefined(scene)){
        $element.find('#slide-'+self.slideIndex).attr(
          'style', 'transform: translate3d('+ scene.translateX +'px, '+ scene.translateY +'px, '+ scene.translateZ +'px);'
        )
      } else {
        self.toOverview()
      }

      self.scene = scene

      return false;
    }

    self.nextScene = function(){
      if(angular.isDefined(self.position)){
        if( self.position < (self.magazineImages[self.slideIndex].scenes.length - 1)){
          self.position++;
        } else{
          if(self.slideIndex < self.magazineImages.length){
            self.toOverview(); 
            self.slideIndex++;
          } 
          else{
            self.toOverview();
          }
        }
      } else{
        self.position = 0;
      };
      // Navigate to Scene
      self.toScene(self.magazineImages[self.slideIndex].scenes[self.position]);
    };

    self.backScene = function(){
      console.log(self.position)
      if(angular.isDefined(self.position)){
        if( self.position > 0){
          self.position--
        } else{
          return self.toOverview()
        }
      } else{
        self.slideIndex > 0 ? self.slideIndex-- : false
        return self.toOverview()
      };
      // Navigate to Scene
      self.toScene( self.magazineImages[self.slideIndex].scenes[self.position] );
    };

    self.toOverview = function(slideIndex){
      var index = slideIndex || self.slideIndex
      // reset variables
      self.position = undefined;
      self.scene = undefined;
      // Return to overview
      $element.find('#slide-'+ index).attr(
        'style', 'transform: translate3d(0px,0px,0px);'
      );
    };

    // Observers

    // Every time slide change
    $scope.$watch("$ctrl.slideIndex", function(newIndex, oldIndex){
      $log.log("Slide Change")
      self.position = undefined      
    })

    // Events
    $element.find('.inner img').on("mousemove", function(event){
      var parentOffset = angular.element(this).parent().offset();
      var elementOffset = angular.element(this).offset();
      //or angular.element(this).offset(); if you really just want the current element's offset
      var relX = event.pageX - parentOffset.left;
      var relY = event.pageY - parentOffset.top;

      var centerX = elementOffset.left + angular.element(this).width() / 2;
      var centerY = elementOffset.top + angular.element(this).height() / 2;

      self.pageX = parseInt(relX)
      self.pageY = parseInt(relY)

      $scope.$apply()
    })

    // Hotkeys

    hotkeys.bindTo($scope)
      .add({
        combo: 'right',
        description: 'To next scene',
        callback: function() {
          self.nextScene(self.slideIndex)
        }
      })
      .add({
        combo: 'left',
        description: 'To next scene',
        callback: function() {
          self.backScene(self.slideIndex)
        }
      });

    return self;
  };

})();

(function() {
  'use strict';

  angular
    .module('hqReader')
    .factory('githubContributor', githubContributor);

  /** @ngInject */
  function githubContributor($log, $http) {
    var apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';

    var service = {
      apiHost: apiHost,
      getContributors: getContributors
    };

    return service;

    function getContributors(limit) {
      if (!limit) {
        limit = 30;
      }

      return $http.get(apiHost + '/contributors?per_page=' + limit)
        .then(getContributorsComplete)
        .catch(getContributorsFailed);

      function getContributorsComplete(response) {
        return response.data;
      }

      function getContributorsFailed(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

